<template>
  <div>
    <!-- swiper -->
    <!--  -->
    <!-- 1.container -->
    <div class="swiper-container">
      <!-- 2. wrapper  -->
      <div class="swiper-wrapper">
        <!--3. slide  -->
        <div class="swiper-slide">
          <img src="../assets/imgs1/6.jpg" alt>
        </div>
        <div class="swiper-slide">
          <img src="../assets/imgs1/7.jpg" alt>
        </div>
        <div class="swiper-slide">
          <img src="../assets/imgs1/8.jpg" alt>
        </div>
        <div class="swiper-slide">
          <img src="../assets/imgs1/9.jpg" alt>
        </div>
        <div class="swiper-slide">
          <img src="../assets/imgs1/10.jpg" alt>
        </div>
        <div class="swiper-slide">
          <img src="../assets/imgs1/11.jpg" alt>
        </div>
      </div>
      <!--  -->
      <div class="swiper-pagination"></div>
      <!--  -->
      <div class="swiper-button-prev"></div>
      <div class="swiper-button-next"></div>
      <!--  -->
      <div class="imgs">
        <div>
          <h5 class="text-primary">My-pizza</h5>
          <p class="text-primary">,</p>
          <button class="btn btn-primary" @click="Admin">Let's Ordet</button>
        </div>
      </div>
    </div>
    <div class="container">
      <div class="row mt-5">
        <div class="col-4">
          <div class="swiper-container1 bg-warning">
            <div class="swiper-wrapper">
              <div class="swiper-slide">
                <img src="../assets/imgs1/1.jpg" alt>
              </div>
              <div class="swiper-slide">
                <img src="../assets/imgs1/3.jpg" alt>
              </div>
              <div class="swiper-slide">
                <img src="../assets/imgs1/4.jpg" alt>
              </div>
              <div class="swiper-slide">
                <img src="../assets/imgs1/5.jpg" alt>
              </div>
              <div class="swiper-slide">
                <img src="../assets/imgs1/6.jpg" alt>
              </div>
            </div>
            <!--  -->
            <div class="swiper-pagination"></div>
            <span class="id">0{{id}}</span>
          </div>
        </div>
        <div class="col-8 box1">
            <h2 class="mb-5">2018</h2>
            <div class="box2">
                <p><img width="27" src="../assets/imgs1/number1.png" alt="">
                <span> </span></p>
                 <p><img width="27" src="../assets/imgs1/number2.png" alt="">
                <span>MINISO 30</span></p>
                 <p><img width="27" src="../assets/imgs1/number3.png" alt="">
                <span> </span></p>
                 <p><img width="27" src="../assets/imgs1/number4.png" alt="">
                <span>Trustdata2018 631</span></p>
                 <p><img width="27" src="../assets/imgs1/number5.png" alt="">
                <span> </span></p>
            </div>
        </div>
      </div>
    </div>
    <div class="container mt-5 mb-5">
        <h3 class="text-center"></h3>
        <Histioy class="kuaiti"></Histioy>
    </div>
    <!--  -->
     <h4 class="text-center"></h4>
  </div>
</template>

<script>
import Swiper from "swiper";
import "../assets/dist/css/swiper.min.css";
import Histioy from './about/Histioy'
export default {
  name: "Home",
  data(){
      return{
          id:1
      }
  },
  mounted() {
    var swiper = new Swiper(".swiper-container", {
      loop: true,
      effect: "fade",
      autoplay: true,
      //
      // 
      pagination: {
        el: ".swiper-pagination",
        clickable: true
      },
      // 
      navigation: {
        nextEl: ".swiper-button-next",
        prevEl: ".swiper-button-prev"
      }
    });
    var that=this
    var swiper = new Swiper(".swiper-container1", {
    
      loop: true,
      direction:"vertical",
      effect: "fade",
       autoplay: {
    delay: 3000,
    stopOnLastSlide: false,
    disableOnInteraction: false,
    },
      //
      // 
      pagination: {
        el: ".swiper-pagination",
        clickable: true
      },
       on: {
          slideChange: function () {

             console.log(this.activeIndex);
           var ind=this.activeIndex;
            if(ind>5){
                ind=1
            }
             if(ind<1){
                ind=5
            }
            console.log(that.id)
            that.id=ind
          },
        },
    });
  },
  methods: {
    Admin() {
      this.$router.push("/admin");
    }
  },
  components:{
      Histioy
  }
};
</script>

<style scoped>
.kuaiti{
    display: flex
}
.kuaiti>.histioyhome{
   flex: 1;
   margin-right: 20px
}  
.id{
    position: absolute;
    display: block;
    right:5px;
    top:60%;
}
.box2 p span{
    display: inline-block;
    font-size:16px;
    line-height:24px;
}
.box2 p span:hover{
    font-weight: bold;
      font-size:18px;
}
.box1{padding-left:100px;}
.swiper-container {
  width: 100%;
  height: 550px;
  position: relative;
}
.swiper-container1 {
    margin-top: 50px;
  width: 400px;
  height:300px;
  position: relative;
}
.swiper-container1 .swiper-wrapper{
    position: absolute;
    top:-25px;
    left: -30px;
}
.swiper-container1 .swiper-pagination{
    top:80%
}
.swiper-slide img {
  width: 100%;
  height: 100%;
}
.imgs {
  width: 360px;
  height: 420px;
  /* background: red; */
  background: url(../assets/imgs1/3.jpg) no-repeat;

  z-index: 99;
  position: absolute;
  top: 100px;
  left: 110px;
}
.imgs div {
  text-align: center;
  position: absolute;
  top: 180px;
  width: 360px;
  height: 111px;
  background-color: #ccc;
  background: rgba(255, 255, 255, 0.6);
}
</style>


