<template>
  <div class="container text-center">
    <img width="100" src="../assets/logo.png">
    <h2 class="text-success"></h2>
    <div class="form-group">
      <input type="text" class="form-control" id="input1" v-model="username" placeholder="">
    </div>
     <div class="form-group">
      <input type="password" class="form-control" id="input2" v-model="password" placeholder="">
    </div>
     <div class="form-group">
      <input type="password" class="form-control" id="input3" v-model="repassword" placeholder="">
    </div>
      <button type="submit" class="btn btn-primary" @click.prevent='submit'></button>
  </div>
</template>

<script>
//axios
import axios from 'axios'
export default {
  name: "Register",
  data(){
      return{
          username:'',
          password:'',
          repassword:''
      }
  },
  methods:{
      submit(){
          if(this.username==''){
              alert('')
          }else if(this.password==''){
              alert('')
          }else if(this.repassword==''){
              alert('')
          }else if(this.password!==this.repassword){
              alert('')
          }else{
              //
            axios.get('./hl-user.json',FormData)
            .then(res=>{
                const result=[]
                console.log(res.data)
                for(let key in res.data){
                    console.log(key)
                    result.push(res.data[key])
                }
                console.log(result)
                const trueUser=result.filter(item=>{
                    return item.username==this.username
                })
                if(trueUser.length>0){
                    alert('')
                }else{
                       //
              const formData={
                  username:this.username,
                  password:this.password
              }
              axios.post('./hl-user.json',formData)
              .then(res=>{
                  alert('')
                  this.$router.push('/login')
              })
                }
            })
           
          }
      }
  }
};
</script>

<style scoped>
.form-control{
    width: 50%;
    display: inline;
}
.btn{
    width: 50%;
    display:inline-block
}
</style>


