<template>
  <div class="row">
    <div class="card mr-2" style="width: 15rem;" v-for="(number,index) in numbers" :key="index">
      <img height="230" class="card-img-top" :src="number.src" alt="Card image cap">
      <div class="card-body">
        <h5 class="card-title text-primary">{{number.name}}</h5>
        <p class="card-text">{{number.num}}</p>
      </div>
       <a href="#" class="btn btn-primary btn-lg btn-block"></a>
    </div>
  </div>
</template>

<script>
export default {
  name: "Activity",
  data() {
    return {
      numbers: [
        { src: "../../../static/imgs/1.jpg", name: "", num: '' },
        { src: "../../../static/imgs/2.jpg", name: "MAX", num:'' },
        { src: "../../../static/imgs/3.jpg", name: "", num:'' }
      ]
    };
  }
};
</script>

<style scoped>
</style>


